@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media screen and ( min-width : 300px ) and ( max-width : 800px ) {

  .body-details {
    align-items: flex-start;
    background-image: url(/Projeto-App-de-receitas/static/media/back.50c033bc.jpg);
    background-size: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 100vw;

  }

  .details-recomendation {
    display: flex;
    flex-flow: row;
    margin-bottom: 40px;
    overflow: auto;
    width: 100vw;
  }

  .recomendation {
    height: 100%;
    margin-top: 3vh;
    width: 181px;
  }

  .recomendation img {
    box-shadow: 0 5px 15px black;
    height: 100%;
    width: 181px;
  }

  .button-iniciar {
    align-items: center;
    background-color: rgba(255, 123, 0);
    border: none;
    border-radius: 4px;
    bottom: 0;
    box-shadow: 0 5px 15px black;
    color: white;
    cursor: pointer;
    display: flex;
    font-family: cursive;
    height: 50px;
    justify-content: space-around;
    letter-spacing: 4px;
    padding: 2px;
    position: fixed;
    text-align: center;
    text-transform: uppercase;
    transition-duration: 0.4s;
    width: 100%;
  }

  .button-iniciar:hover {
    box-shadow: 0 2px 10px 5px black;
    color: black;
  }

  .linkShare input {
    background-color: transparent;
    border: transparent;
    color: transparent;
    height: 1;
    width: 1;
  }

  .buttons-detail {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-start;
    margin: 5px;
    padding: 0;
  }

  .buttons-detail button {
    background-color: transparent;
    border: none;
    margin: 5px;
    padding: 0;
    text-decoration: none;
  }

  .buttons-detail img {
    margin: 10px 2px;
    padding-bottom: 0;
    -webkit-padding-start: 8px;
            padding-inline-start: 8px;
    width: 10vw;
  }

  .recipe-photo {
    align-self: center;
    box-shadow: 0 5px 15px black;
    width: 90vw;
  }

  .recipe-title {
    align-items : center;
    background-color: rgba(255, 123, 0);
    box-shadow: 0 0 15px black;
    color: white;
    display: flex;
    font-family: Lobster , cursive;
    font-size: xx-large;
    height: -webkit-max-content;
    height: max-content;
    justify-content: center;
    letter-spacing: 4px;
    margin: 0;
    padding: 10px;
    text-shadow: 2px 0 5px black , -2px 0 5px black , 0 2px 5px black , 0
      -2px 5px black , 1px 1px 5px black , -1px -1px 5px black , 1px -1px 5px black , -1px
      1px 5px black;
    width: 100vw;
  }

  .recipe-category {
    font-family: Lobster , cursive;
    font-size: x-large;
    letter-spacing: 4px;
    margin: 5vw;
  }

  .ingredients-list {
    -webkit-margin-start: 5vw;
            margin-inline-start: 5vw;
    margin-top: -1.5vh;
  }

  .ingredients-list h2 {
    margin: 0;
  }

  .ingredients-list li {
    font-size: large;
  }

  .instructions {
    align-self: center;
    margin-bottom: 5vh;
    width: 90vw;
  }

  .instructions h2 {
    margin: 0 0 1vh;
  }

  .video-recipe {
    align-self: center;
    box-shadow: 0 5px 15px black;
    height: 25vh;
    margin-bottom: 3vh;
    width: 90vw;
  }

  .msg-share {
    margin-top: 0;
    padding-left: 20px;
    padding-top: 0;
  }
}

.category-button-body {
  align-items: center;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  width: 88vw;
}

.category-button-body button {
  align-items: center;
  background-color: rgba(255, 123, 0, 0.9);
  border: none;
  border-radius: 4px;
  box-shadow: 0 5px 15px black;
  color: white;
  cursor: pointer;
  display: flex;
  font-family: verdana;
  font-size: xx-small;
  height: 7vh;
  justify-content: center;
  letter-spacing: 4px;
  margin: 10px;
  overflow: auto;
  padding: 0;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  transition-duration: 0.4s;
  width: 23.5vw;
}

.category-button-body button:hover {
  box-shadow: 0 2px 10px 5px black;
  color: black;
}

.category-button-body button:active:after {
  opacity: 1;
  padding: 0;
  transition: 0s;
}

.category-button-body button:focus { outline: 0; }

@media only screen and ( min-width : 300px ) and ( max-width : 800px ) {

  .footer-menu {
    align-items: center;
    background-color: rgba(255, 123, 0);
    bottom: 0;
    display: flex;
    height: 7vh;
    justify-content: space-around;
    padding: 2px;
    position: fixed;
    width: 100%;
  }
}

.form-search {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 2vh;
}

.search-input {
  align-self: center;
  font-family: cursive;
  margin-bottom: 2vh;
  width: 90vw;
}

.theInput {
  background: transparent;
  border: 0;
  border-bottom: 2px solid #9b9b9b;
  color: black;
  font-family: cursive;
  font-size: 1.3rem;
  outline: 0;
  transition: border-color 0.2s;
  width: 90vw;
}

.button-search {
  align-items: center;
  align-self: center;
  background-color: rgba(255, 123, 0, 0.9);
  border: none;
  border-radius: 4px;
  box-shadow: 0 5px 15px black;
  color: white;
  cursor: pointer;
  display: flex;
  font-family: cursive;
  font-size: xx-small;
  height: 7vh;
  justify-content: center;
  letter-spacing: 4px;
  margin: 10px;
  margin-bottom: 2vh;
  overflow: auto;
  padding: 0;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  transition-duration: 0.4s;
  width: 90vw;
}

.button-search:hover {
  box-shadow: 0 2px 10px 5px black;
  color: black;
}

.button-search:active:after {
  opacity: 1;
  padding: 0;
  transition: 0s;
}

.button-search:focus { outline: 0; }

@media screen and ( min-width : 300px ) and ( max-width : 800px ) {

  .header-home-page {
    align-items : center;
    background-color: rgba(255, 123, 0);
    display: flex;
    font-family: Lobster , cursive;
    font-size: x-large;
    height: 10vh;
    justify-content: space-between;
    width: 100vw;
  }

  .explore-button {
    background-color: transparent;
    border: none transparent 0;
    color: transparent;
  }

  .icons {
    margin: 0 10px;
  }

  .header-home-page h2 {
    color: white;
    text-shadow: 2px 0 5px black , -2px 0 5px black , 0 2px 5px black , 0
      -2px 5px black , 1px 1px 5px black , -1px -1px 5px black , 1px -1px 5px black , -1px
      1px 5px black;
  }
}

.section-recipes {
  align-items: center;
  background-color: rgba(255, 250, 250, 0.5);
  border: none;
  border-radius: 10px;
  box-shadow: 0 5px 15px black;
  cursor: pointer;
  display: flex;
  flex-flow: row wrap;
  font-family: cursive;
  justify-content: center;
  letter-spacing: 4px;
  margin: 10px;
  overflow: auto;
  padding: 0;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  width: 90vw;
}

.body-recipes {
  align-items: center;
  border: solid #f1ef7e 0;
  border-radius: 1px;
  box-shadow: 0 5px 15px black;
  display: flex;
  flex-flow: column nowrap;
  height: -webkit-max-content;
  height: max-content;
  justify-content: flex-start;
  margin: 15px;
}

.body-recipes img {
  border-radius: 1px 1px 0 0;
  width: 73vw;
}

.body-recipes span {
  align-items: center;
  background-color: rgba(255, 123, 0, 0.8);
  display: flex;
  font-family: verdana;
  font-size: 100%;
  height: 100%;
  justify-content: flex-start;
  padding: 5px;
  width: 70vw;
}

.link {
  color: rgba(0, 0, 0);
  text-decoration: none;
}

@media screen and ( min-width : 300px ) and ( max-width : 800px ) {

  .header-explore-home-page {
    align-items: center;
    background-color: rgb(255, 55, 55);
    display: flex;
    justify-content: space-between;
    padding: 0 7px;
  }
}

.body-list-recipes {
  align-items: center;
  background: url(/Projeto-App-de-receitas/static/media/back.50c033bc.jpg);
  background-size: 60vh;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: flex-start;
  margin-bottom: 7.5vh;
  overflow-y: auto;
}

.log-container {
  align-items: center;
  display: flex;
  height: 100vh;
  justify-content: center;
  width: 100vw;
}

