.body-progress {
  background-image: url(/Projeto-App-de-receitas/static/media/back.50c033bc.jpg);
  background-size: 60vh;
  color: rgb(58, 58, 58);
}

.instructions-progress {
  margin-bottom: 10vh;
}

.ingredients-progress {
  list-style-type: none;
}

